var baseUrl = require("../../config");

export default {
  name: "pay-info",
  data: function() {
    return {};
  },
  created() {
    let config = {};
    config.url = window.location.href;
    // 判断当前url是否存在?参数匹配符
    if (!config.url.match(/\?/)) {
      location.replace(
        window.location.href.split("#")[0] + "?" + window.location.hash
      );
      return;
    }
  },
  mounted() {},
  methods: {
    pay() {
      if (!this.$route.query.orderId) {
        // 显示文字
        return this.$vux.toast.text("没有订单id");
      }
      this.$http
        .post(
          baseUrl.apiBaseUrl +
            "api/wxpay/prepay?user_id=" +
            this.$route.query.userId +
            "&total_fee=" +
            this.$route.query.prize +
            "&orderId=" +
            this.$route.query.orderId,
          {}
        )
        .then(({ data: res }) => {
          if (res.code == "0000") {
            WeixinJSBridge.invoke(
              "getBrandWCPayRequest",
              {
                appId: "wx2517d7a8920ab213",
                timeStamp: res.result.timestamp,
                // 支付签名随机串，不长于 32 位
                nonceStr: res.result.nonceStr,
                // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
                package: res.result.package,
                // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                signType: "MD5",
                // 支付签名
                paySign: res.result.paySign
              },
              res => {
                //支付成功或失败前台判断
                if (res.err_msg == "get_brand_wcpay_request:ok") {
                  this.$vux.toast.text("支付成功！", "top");
                  this.$router.back(-1);
                } else {
                  this.$vux.toast.text("支付失败！", "top");
                }
              }
            );
          } else {
            this.$vux.alert.show({
              title: "提示",
              content: res.msg
            });
          }
        });
    }
  }
};



