
import Selector from 'vux/src/components/selector/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import $ from "jquery";
import Clipboard from "clipboard";
import CountDown from "./count-down.vue";
import ContentWrap from "./content-wrap.vue";
import Data from "./eData.js";
import Scroll from "./scroll.vue";
var baseUrl = require("../../config");
export default {
name: "app-egg",
data: function() {
return {
userName: "",
prize: null,
address: "",
shown: false,
shown2: false,
ruleShow: false,
pshown: false,
alertShow: false,
alertContent: "",
loading: false,
tousuDialog: false,
supportDialog: false,
appDialog: false,
home: true,
ispreview: false,
users: [],
user_id: "",
b_userId: "",
shareId: "",
params: {},
orderList: [],
packageList: [],
order: [],
orderId: null,
formD: Data,
isLoading: true,
name: "",
tel: "",
type: "",
options: [
{
key: 0,
value: "新会（学）员"
},
{
key: 1,
value: "旧会（学）员"
}
],
list: [],
musicUrl: ""
};
},
props: {
form: {
type: Object
}
},
mounted() {
this.query();
},
methods: {
eggInit() {
var post = this.$http.post;
var user_id = this.user_id;
var activityid = this.params.id;
var $hammer = $("#hammer"),
$tips = $(".info"),
$eggList = $(".egg li"), //金蛋父级
$egg = $(".goldegg"), //金蛋
$change = $("#change"), //剩余次数
$des = $("#des"), //剩余次数
length = $egg.length,
data = { count: parseInt(this.formD.prizeLeft) || 0 }, //次数
arr = [],
openArr, //记录被砸开的蛋的下标数组
rem = 75;
$change.html(data.count);
$egg.eq(length).addClass("jump");
$tips.eq(length).show();
setInterval(function() {

length++;
length %= 9;
$egg.eq((length - 1 < 0 && 8) || length - 1).removeClass("jump");
$tips.eq((length - 1 < 0 && 8) || length - 1).hide();
reback();
$egg.eq(length).addClass("jump");
$tips.eq(length).show();
}, 1000);

function reback() {
if (!$egg.eq(length).hasClass("init")) {

length++;
length %= 9;
reback();
}
}

/*砸蛋事件*/
for (var i = 0; i < length; i++) {
$egg.eq(i).data("mark", true); 
$eggList.eq(i).data("i", i);
$eggList.eq(i).click(function() {

if (data.count > 0) {
if ($egg.eq($(this).data("i")).data("mark")) {
ajax($(this));
} else {
alert("这枚金蛋已经被您砸开了");
}
} else {
alert("您当前砸蛋次数为0，无法砸蛋");
}
});
}

function ajax($el) {
post(baseUrl.apiBaseUrl + "lottery/lottery", {
user_id: user_id,
activityId: activityid
}).then(({ data: res }) => {
if (res.code == "0000") {
let _data = res.result.data;
$des.html(`${_data.giftName},  ${_data.giftDescription}`);
if (!!_data.url) {
$(".win").attr("background-image", `url(${_data.url})`);
}
eggChange($el.data("i"), res.result.data);
} else {
alert(res.msg);
}
});
}
/*砸蛋事件的处理*/
function eggChange(i, _data) {

data.count--;
$change.html(data.count);
$hammer.removeClass("shak"); 

(i === 0 || i === 3 || i === 6) &&
$hammer.css("left", 165 / rem + "rem");
(i === 1 || i === 4 || i === 7) &&
$hammer.css("left", 415 / rem + "rem");
(i === 2 || i === 5 || i === 8) &&
$hammer.css("left", 665 / rem + "rem");
(i === 0 || i === 1 || i === 2) && $hammer.css("top", 60 / rem + "rem");
((i === 3) | (i === 4) || i === 5) &&
$hammer.css("top", 280 / rem + "rem");
((i === 6) | (i === 7) || i === 8) &&
$hammer.css("top", 500 / rem + "rem");

setTimeout(function() {
$hammer.css({
left: 665 / rem + "rem",
top: 60 / rem + "rem"
});
}, 1500);


setTimeout(function() {
$hammer.addClass("hit");
setTimeout(function() {
$egg
.eq(i)
.prop(
"src",
"data:image/png;base64,"
);
}, 300);
setTimeout(function() {
$egg.eq(i).removeClass("init");
win();
$hammer.removeClass("hit"); 
$hammer.addClass("shak");


arr.push(i); 

}, 600);
}, 600);
$egg.eq(i).data("mark", false); 
}

function win() {
$("#mask").show();
$("#mask .winning").addClass("reback");
setTimeout(() => {
$("#mask #card").addClass("pull");
}, 500);
}
},
initQQMap() {
wx.ready(() => {
wx.openLocation({
latitude: this.formD.latitude, // 纬度，浮点数，范围为90 ~ -90
longitude: this.formD.longitude, // 经度，浮点数，范围为180 ~ -180。
name: "活动地点", // 位置名
address: this.address, // 地址详情说明
scale: 10, // 地图缩放级别,整形值,范围从1~28。默认为最大
infoUrl: `http://apis.map.qq.com/uri/v1/marker?marker=coord:${
this.formD.latitude
},${this.formD.longitude};title:活动地点;addr:${this.address}` // 在查看位置界面底部显示的超链接,可点击跳转
});
});
},
mapInit() {
var self = this;
if (self.formD.latitude != "" && self.formD.longitude != "") {
var map = new qq.maps.Map(document.getElementById("showPosition"), {
center: new qq.maps.LatLng(39.916527, 116.397128),
zoom: 13,
panControl: false,
zoomControl: false,
scaleControl: false
});

var marker = new qq.maps.Marker({
position: new qq.maps.LatLng(
self.formD.latitude,
self.formD.longitude
),
map: map
});
var geocoder = new qq.maps.Geocoder({
complete: function(result) {
map.setCenter(result.detail.location);
self.address = result.detail.address;
}
});
geocoder.getAddress(
new qq.maps.LatLng(self.formD.latitude, self.formD.longitude)
);
}
},
close() {
this.supportDialog = false;
this.appDialog = false;
},
support() {
this.supportDialog = true;
},
goapp() {
this.appDialog = true;
},
phone() {
this.pshown = true;
},
showntousu() {
this.tousuDialog = true;
},
tousu() {
this.$vux.toast.text("投诉成功！", "top");
this.tousuDialog = false;
},
query() {
if (!!this.form) {
this.formD = this.form;
this.ispreview = true;
this.mapInit();
return;
}
var params = this.$route.query;
this.params = params;

if (!params.id) {
return console.log("id is null");
}
this.user_id = params.userId;
this.shareId = this.params.shareId;
if (!!this.user_id) {
this.$http
.get(baseUrl.apiBaseUrl + "sys/user/info/" + params.userId)
.then(({ data: res }) => {
if (res.code != "0000") {
console.log("获取用户信息失败");
} else {
this.userName = res.user.username || res.user.nickname;

this.loading = true;
this.$http
.post(baseUrl.apiBaseUrl + "lottery/info", {
activityId: params.id,
user_id: params.userId
})
.then(({ data }) => {
if (data.code == "0") {
this.formD = data.lottery;
this.mapInit();
var fore = data.lottery;
document.title = fore.activityName;
var thumbnail = data.lottery.thumbnail;
this.$nextTick(() => {
this.eggInit();
});
}
});
}
});
}
},
eggclose() {
$("#mask").hide();
$("#mask .winning").removeClass("reback");
$("#mask #card").removeClass("pull");
}
},
components: {
Selector,
XDialog,
Alert,
Loading,
Group,
Cell,
Loading,
Datetime,
XInput,
XNumber,
XSwitch,
XTextarea,
Scroll,
ContentWrap,
CountDown
}
};



